// ÂØπË±°Ê∑±Êã∑Ë¥ù
export const objDeepCopy = function (obj) {
  return JSON.parse(JSON.stringify(obj));
};

// ÊàëÈúÄË¶Å‰∏Ä‰∏™‰ºöÊîπÂèòÂéüÊï∞ÁªÑÁöÑËøáÊª§
const arrayFilert = function (list, fn) {
  for (let i = 0; i < list.length; ) {
    if (!fn(list[i], i)) {
      list.splice(i, 1);
    } else {
      i++;
    }
  }
  return list;
};

/**
 * Áî®Êù•ËøáÊª§Êï∞ÁªÑÂºÄÂßãÁöÑÊ†ëÁªìÊûÑÊï∞ÊçÆÔºåÈááÁî®ÁöÑÂâîÈô§‰∏çÁõ∏Âπ≤ÊûùÂπ≤ÁöÑÈÄªËæëÔºåÊâÄ‰ª•‰ºöÊîπÂèòÂéüÊï∞ÁªÑÔºå‰ΩøÁî®Êó∂ËØ∑‰º†ÂÖ•Êï∞ÁªÑÁöÑÂâØÊú¨
 * @param {Array} treeList Ë¶ÅËøáÊª§ÁöÑÊ†ë
 * @param {Funtction} fn ËøáÊª§ËßÑÂàô
 */
const deepFilter = function (treeList, fn) {
  return arrayFilert(treeList, (item) => {
    if (item.children && item.children.length) {
      deepFilter(item.children, fn);
      if (item.children.length) {
        return true;
      } else {
        // ‰πãÂâçÁöÑÈÄªËæëÊòØÂÆÉÂè™ÊúâÂú®Â±û‰∫éÁõÆÂΩïÁöÑÊó∂ÂÄôÊâç‰ºöÊúâchildrenÔºåÊâÄ‰ª•‰∏çÈúÄË¶ÅËÄÉËôëÊú¨Ë∫´ÂåπÈÖçÁöÑÊÉÖÂÜµ
        // Ê≠§Á≥ªÁªü‰∏≠ËèúÂçï‰∏ãËøòÂèØËÉΩ‰ºöÊúâËèúÂçïÔºåÊâÄ‰ª•Ë¶ÅÂ§ÑÁêÜ‰ªñËá™Â∑±Â∞±ËÉΩÂåπÈÖçÁöÑÊÉÖÂÜµ
        return fn(item);
      }
    } else {
      return fn(item);
    }
  });
};

/** deepFilterÁöÑÂåÖË£ÖÔºå‰ΩøÊñπÊ≥ï‰∏çÊîπÂèò‰º†ÂÖ•ÁöÑÊï∞ÊçÆ  */
export const treeFilter = function (treeList, fn) {
  return deepFilter(objDeepCopy(treeList), fn);
};

// Â∞ÜÊ†ëËΩ¨Êàê‰∏ÄÁª¥Êï∞ÁªÑ
export const flattenDeep = (array = []) => {
  const list = [];

  const deep = function (obj) {
    if (obj.type === "1") {
      // eslint-disable-next-line no-unused-vars
      const { children, ...item } = obj;
      list.push(item);
    }
    if (obj.children && obj.children.length) {
      obj.children.forEach((item) => deep(item));
    }
  };

  array.forEach((item) => deep(item));

  return list;
};
